import Vue from 'vue'
import Router from 'vue-router'
import RadarGallery from '@/components/radarGallery/radarGallery'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/gallery',
      name: 'RadarGallery',
      component: RadarGallery
    }
  ]
})



